<template>
  <div>
    <div class="contenedor-foto">
      <Fotoeditar></Fotoeditar>
    </div>
    

    <h1>Hola {{datos.nombre}} {{datos.apellido}}</h1>
    <p></p>
    <a @click="logout">Logout</a>
  </div>
</template>

<script>
import Fotoeditar from '@/components/Fotoeditar.vue'

import firebase from "firebase";
const db = firebase.firestore();

export default {
  /* firebase: {
    
  }, */
  name:'Account',
  components: {
    Fotoeditar
  },
  data() {
    return {
      nombre: "Usuario",
      datos: {
        nombre: "",
        apellido: "",
      },
    };
  },
  methods: {
    logout() {
      firebase
        .auth()
        .signOut()
        .then(() => this.$router.replace("login"));
    },
    async getNombre() {

      
      var docs = await db
        .collection("nuevosdatos")
        .where("id_user", "==", firebase.auth().currentUser.uid)
        .get();
      var id;
      var nom;
      var apell;

      docs.forEach(function (doc) {
        id = doc.id;
        nom = doc.data().nombre;
        apell = doc.data().apellido;
        console.log(doc.id, " => ", doc.data().nombre);
        
      });
      this.datos.nombre = nom;
      this.datos.apellido = apell;
    },
  },
  beforeMount() {
    this.getNombre();
  },
};
</script>



<style scoped>
h1 {
  color: rgb(40, 94, 78);
}
.contenedor-foto {
  display: flex;
  justify-content: center;
}
</style>